import React from 'react';
import { FaAngleDown } from 'react-icons/fa';
interface Step {
  image: {
    publicURL: string;
  };
  description: string;
  last?: boolean;
}

interface Props {
  steps: Step[];
  title: string;
}

const StepCard = ({ image, description, last }: Step) => (
  <div data-sal="fade" data-sal-delay="300">
    <div className="p-8 md:flex md:items-center">
      <img className="w-48 mx-auto p-2" src={image.publicURL} />
      <p className="text-justify md:px-8">{description}</p>
    </div>
    {!last && <FaAngleDown className="text-5xl mx-auto" />}
  </div>
);

const Concept = ({ steps, title }: Props) => (
  <div className="text-blue p-8 py-20 md:p-20 max-w-4xl mx-auto">
    <h2 className="text-center font-brand pb-4 md:pb-12">{title}</h2>
    {steps.map((step, index) => (
      <StepCard
        image={step.image}
        description={step.description}
        last={index == steps.length - 1}
      />
    ))}
  </div>
);

export default Concept;
