import React from 'react';

interface Props {
  products: Product[];
  title: string;
}
interface Product {
  title: string;
  description: string;
  image: {
    publicURL: string;
  };
}

const ProductCard = ({ title, description, image }) => (
  <div
    data-sal="slide-up"
    data-sal-delay="300"
    className="bg-white p-8 rounded shadow border-4 border-white border-opacity-50 hover:border-orange-light mt-4 md:mt-0 md:mx-4 md:max-w-md cursor-pointer"
  >
    <div className="w-24 mx-auto">
      <img src={image.publicURL} alt={title} />
    </div>
    <h3 className="text-center text-blue py-6">{title}</h3>
    <p className="text-justify">{description}</p>
  </div>
);

const Products = ({ title, products }: Props) => (
  <div className="bg-grey-light p-8 py-20 md:p-20">
    <h2 className="text-center text-blue font-brand pb-4 md:pb-12">{title}</h2>
    <div className="md:flex justify-center">
      {products.map(product => (
        <ProductCard
          title={product.title}
          description={product.description}
          image={product.image}
        />
      ))}
    </div>
  </div>
);

export default Products;
