import React from 'react';
import { FaRocket } from 'react-icons/fa';
import scrollTo from 'gatsby-plugin-smoothscroll';

import heroImage from '../images/hero.svg';

interface Props {
  content: {
    title: string;
    description: string;
  };
}

const Hero = ({ content }: Props) => (
  <div className="min-h-screen">
    <div className="flex flex-col md:flex-row-reverse md:items-center">
      <div className="h-80 self-end md:hero-image">
        <img src={heroImage} className="h-full max-w-none -m-8" />
      </div>
      <div className="mx-auto text-center p-4 md:p-16 md:-mr-16">
        <h1 className="lg:text-6xl">{content.title}</h1>
        <p>{content.description}</p>
        <button
          onClick={() => scrollTo('#newsletter')}
          className="bg-orange text-white text-sm p-4 rounded-full mt-6 max-w-max flex items-center mx-auto transition duration-500 ease-in-out transform hover:-translate-y-1 hover:scale-110"
        >
          Join waitlist
          <FaRocket className="ml-2" />
        </button>
      </div>
    </div>
  </div>
);

export default Hero;
