import React from 'react';

interface Props {
  content: {
    title: string;
    description: string;
    url: string;
  };
}

const Video = ({ content }: Props) => (
  <div className="bg-blue text-white p-8 py-20 md:p-20">
    <h2 className="text-center font-brand pb-4 md:pb-12">{content.title}</h2>
    <div className="block md:flex items-center">
      <div className="flex-1">
        <iframe
          width="100%"
          height="315"
          src={content.url}
          frameBorder="0"
          allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture"
          allowFullScreen
        />
      </div>
      <p className="flex-1 pt-8 text-justify md:p-8">{content.description}</p>
    </div>
  </div>
);

export default Video;
