import React from 'react';
import { FaFilePdf } from 'react-icons/fa';

interface Props {
  title: string;
  description: string;
  file: {
    publicURL: string;
  };
  pdfButton: {
    label: string;
  };
}

const Whitepaper = ({ title, description, file, pdfButton }: Props) => (
  <div className="bg-blue text-white p-8 py-20 md:p-20">
    <h2 className="text-center font-brand pb-4 md:pb-12">{title}</h2>
    <p className="text-justify max-w-lg mx-auto">{description}</p>
    <a
      href={file.publicURL}
      target="_blank"
      rel="noreferrer"
      className="bg-orange text-sm p-4 rounded-full mt-6 max-w-max flex items-center mx-auto transition duration-500 ease-in-out transform hover:-translate-y-1 hover:scale-110"
    >
      {pdfButton.label}
      <FaFilePdf className="ml-2" />
    </a>
  </div>
);

export default Whitepaper;
