import React from 'react';

interface Props {
  title: string;
  description: string;
  form: {
    firstNameLabel: string;
    emailLabel: string;
    buttonLabel: string;
  };
}

const Newsletter = ({ title, description, form }: Props) => (
  <div className="bg-grey-light text-blue p-8 py-20 md:p-20" id="newsletter">
    <h2 className="text-center  font-brand pb-4 md:pb-12">{title}</h2>
    <p className="max-w-xl p-2 text-justify mx-auto">{description}</p>
    <form className="flex flex-col max-w-xl mx-auto mt-2" id="newsletter_form">
      <input
        type="text"
        name="firstName"
        id="firstName"
        placeholder={`${form.firstNameLabel}*`}
        required
        className="p-4 m-2 rounded focus:ring-2 focus:ring-blue"
      />
      <input
        type="email"
        name="email"
        id="email"
        placeholder={`${form.emailLabel}*`}
        required
        className="p-4 m-2 rounded focus:ring-2 focus:ring-blue"
      />
      <input
        className="invisible"
        autoComplete="off"
        type="text"
        id="name"
        name="name"
        placeholder="Your name here"
      />
      <input
        className="invisible"
        autoComplete="off"
        type="email"
        id="email"
        name="email"
        placeholder="Your e-mail here"
      />
    </form>
    <button
      type="submit"
      form="newsletter_form"
      value="Submit"
      className="bg-blue text-white p-4 rounded-full max-w-max flex items-center mx-auto transition duration-500 ease-in-out transform hover:-translate-y-1 hover:scale-110"
    >
      {form.buttonLabel}
    </button>
  </div>
);

export default Newsletter;
